.card {
  border-radius: 8px;
  overflow: hidden;
  background: var(--cardGray);
  text-align: center;
  padding: 1.7em 0;
  position: relative;
}

.card::before {
  content: "";
  height: 4px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}

.card.facebook::before{
  background-color: var(--facebook);
}

.card.twitter::before{
  background-color: var(--twitter);
}

.card.instagram::before{
  background-image: linear-gradient(to right, var(--instagram-1) 0%, var(--instagram-2) 100%);
}

.card.youtube::before{
  background-color: var(--youtube);
}

.card-title {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--cardTitle);
  font-size: .9em;
  font-weight: bold;
}

.card-title img {
  margin-right: 5px;
}

.card-followers-number {
  font-size: 50px;
  font-weight: 700;
}

.card-followers-title {
  display: block;
  text-transform: uppercase;
  color: var(--gray);
  letter-spacing: 5px;
  font-size: .75em;
}

.card-today {
  display: flex;
  margin-top: 20px;
  color: var(--lime-green);
  font-size: .8em;
  font-weight: bold;
  align-items: center;
  justify-content: center;
}

.card-today img {
  margin-right: 5px;
}